.shortcut-buttons-flatpickr-wrapper .shortcut-buttons-flatpickr-buttons .shortcut-buttons-flatpickr-button:disabled:hover {
  color: #999;
}
.shortcut-buttons-flatpickr-wrapper .shortcut-buttons-flatpickr-buttons .shortcut-buttons-flatpickr-button:hover {
  text-decoration: underline;
}

body {
  background-color: #EEE;
  font-family: arial, verdana, helvetica, sans-serif;
  text-align: left;
}

h1 {
  font-size: 161.6%;
  font-weight: bold;
}

h2 {
  background-color: #eee;
  border-radius: 4px;
  font-size: 146.5%;
  font-weight: bold;
  padding: 0.5em;
  text-shadow: 1px 1px 0 #FFF;
}

h3 {
  color: #003366;
  font-size: 131%;
  font-weight: bold;
}

hr {
  margin-bottom: 0.5em;
  margin-top: 0.5em;
}

.installer-main {
  background-color: #FFF;
  border-radius: 5px;
  box-shadow: 0 2px 2px 0 rgba(50, 50, 50, 0.5);
  margin-bottom: 3%;
  margin-top: 3%;
  padding: 1em;
}

ul,
ol {
  padding: 5px 5px 5px 20px;
}

#logo {
  background: url(../img/koha-logo.gif) no-repeat top center;
  margin-bottom: 0.5em;
  margin-top: 0;
}
#logo a {
  border-bottom: none;
  display: block;
  height: 74px;
  text-indent: -1000px;
}

.bg-danger {
  padding: 0.2em 0.4em;
}

.step a,
.step input {
  font-size: 2em;
}

td input {
  font-size: 1.5em;
}

.step,
th {
  text-align: right;
}

#footer {
  border-top: 1px solid #ccc;
  font-style: italic;
  padding-top: 1em;
  text-align: center;
}

.update_error {
  color: red;
  font-weight: bold;
}

.checkbox {
  margin-bottom: 1em;
}

fieldset.rows {
  border-width: 0;
  clear: left;
  float: left;
  font-size: 90%;
  margin: 0.9em 0 0 0;
  padding: 0;
  width: 100%;
}
fieldset.rows legend {
  border: 0;
  font-size: 110%;
  font-weight: bold;
  margin-bottom: 0;
  margin-left: 1em;
}
fieldset.rows label {
  float: left;
  font-weight: bold;
  margin-right: 1em;
  text-align: right;
  width: 9em;
}
fieldset.rows label.error {
  color: #cc0000;
  float: none;
  margin-left: 1em;
  width: auto;
}
fieldset.rows span.label {
  border-radius: 0;
  color: inherit;
  display: inline;
  float: left;
  font-size: inherit;
  font-weight: bold;
  line-height: 1;
  margin-right: 1em;
  padding: 0.2em 0.6em 0.3em;
  text-align: right;
  vertical-align: baseline;
  white-space: normal;
  width: 9em;
}
fieldset.rows fieldset {
  background-color: transparent;
  font-size: 100%;
  margin: 1em;
  padding: 0.3em;
}
fieldset.rows ol {
  list-style-type: none;
  padding: 0 1em 0 1em;
}
fieldset.rows li {
  clear: left;
  float: left;
  list-style-type: none;
  padding-bottom: 1em;
  width: 100%;
}
fieldset.rows .hint {
  color: #666;
  font-size: 96%;
  margin-left: 10.5em;
  margin-right: 4em;
  margin-top: 0.5em;
}

label.required,
span.required {
  color: #C00;
}

span.required {
  font-style: italic;
  margin-left: 0.5em;
}

.breadcrumbs {
  background-color: #e6f0f2;
  margin: 1em 0;
  padding: 0.4em 2em;
}

/* Font Awesome isn't used in the installer, so add custom X icon */
.clear_date {
  background-image: url("data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 3.175 3.175' xmlns='http://www.w3.org/2000/svg'%3E%3Cg transform='translate(-74.712 -91.722)'%3E%3Cpath d='M75.321 94.571a.207.207 0 0 0 .293 0l.686-.685.685.685a.207.207 0 0 0 .293 0l.284-.283a.207.207 0 0 0 0-.294l-.686-.685.686-.685a.207.207 0 0 0 0-.294l-.284-.283a.207.207 0 0 0-.293 0l-.685.685-.686-.685a.207.207 0 0 0-.293 0l-.283.283a.207.207 0 0 0 0 .294l.685.685-.685.685a.207.207 0 0 0 0 .294z' fill='%23c33'/%3E%3Cimage x='-117.25' y='-204.6' width='196.44' height='175.61' preserveAspectRatio='none'/%3E%3C/g%3E%3C/svg%3E");
  background-position: 5px 5px;
  background-repeat: no-repeat;
  content: "";
  display: inline-block;
  width: 16px;
  height: 16px;
}

span.breadcrumbs {
  display: block;
}

.selectall,
.deselectall {
  display: inline-block;
}

.selectall.mandatory,
.deselectall.optional {
  display: none;
}

#steps {
  border: 1px solid #EEE;
  border-radius: 5px;
  padding: 1em;
  margin: 1em 0;
}
#steps ol {
  font-size: 1.2rem;
  list-style-type: none;
  margin: 0;
}

.open {
  color: #888;
}

.current {
  color: #000;
  font-weight: bold;
}

.complete {
  color: green;
}